.container {
    flex-basis: 30%;
    height: 300px;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

.logo {
    width: 50px;
    height: 50px;
}

.title {
    margin-left: 10px;
    color: white;
}

.description {
    margin-left: 10px;
    color: white;
}

.showButton {
    text-decoration: none;
    background-color: #a7a3a3;
    color: black;
    padding: 10px 20px;
    border: 1px solid black;
    border-radius: 5px;
}

.header {
    width: 100%;
    height: 70%;
    background-color: #736969;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.footer {
    width: 100%;
    height: 30%;
    background-color: cadetblue;
}

@media (max-width: 768px) {
    .logo {
        display: none;
    }
    .container {
        height: 500px;
    }
    .header {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .footer {
        padding-top: 20px;
        padding-bottom: 20px;
    }
}